// Flex
.flex {
    display: flex;
    &.justify-between{
        justify-content: space-between;
    }
    &.justify-center{
        justify-content: center;
    }
    &.items-center{
        align-items: center;
    }
    &.flex-col{
        flex-direction: column;
    }
    &.flex-wrap{
        flex-wrap: wrap;
    }
}

.pointer{
    cursor: pointer;
}

// Text
.vertical-text {
    writing-mode: tb-rl;
    transform: rotate(-180deg);
}

/* list */

.clean-list {
    list-style-type: none;
    margin: 0;
    padding: 0;
}

/* OTHERS */

.uppercase {
    text-transform: uppercase;
}

.cursor-pointer {
    cursor: pointer;
}

.text-center {
    text-align: center;
}

.round {
    border-radius: 5px;
}

